import Util from "../std/Util.js";
import Dump from "../std/Dump.js";
import inflate from "pako";

function loadStars(settings, callback) {
    let xhr  = new XMLHttpRequest();
    let compressed = true;
    let file = "data/galaxy_all.json.gz";
    if (settings.galaxy == "small") {
        compressed = false;
        file = "data/idp_nearby40pc.json";
    } else {
        xhr.responseType = 'arraybuffer';
    }
    xhr.open('GET', file, true);
    Util.log("Getting file:"+file);
    xhr.onload  = function() {
        let resp = xhr.response;
        if (compressed) {
            resp  = inflate.inflate(resp, {to:'string'});
        } else {            
            Util.log("Got responseText:"+resp.length);
        }
        let json = JSON.parse(resp);
        Util.log("Parsed");
        let zz;
        for (zz in json) {
            break;
        }
        callback(json[zz]);
    };
    xhr.onerror = function() {
        Util.log("Called error function when loading stars!");
    }
    
    xhr.send(null);
}

export default loadStars;

